import Framework from '../lib/Live2DFramework.js'
import PlatformManager from './PlatformManager.js'
import LAppModel from './LAppModel.js'
import LAppDefine from './LAppDefine.js'
const {
    Live2DFramework
} = Framework

function LAppLive2DManager(live) {
    // console.log("--> LAppLive2DManager()");

    this.live = live;
    this.models = [];

    this.count = -1;
    this.reloadFlg = false;
    Live2D.init();
    Live2DFramework.setPlatformManager(new PlatformManager(this.live));

}

LAppLive2DManager.prototype.createModel = function () {


    var model = new LAppModel();
    this.models.push(model);

    return model;
}


